<script>
import personalApi from '@/http/service/index';
// import util from '@/utils/utils';

const CommonAlert = () => import('@/components/alert/common-alert.vue')
export default {
  components: {
    'Common-alert': CommonAlert,
  },
  mounted: function () {
    // 传网单id
    if (this.$route.query.orderProductId) {
      // 网单id赋值
      this.orderProductId = parseInt(this.$route.query.orderProductId)
      this.details()
    }
  },
  data() {
    return {
      typeActualStr: true, // 状态为3且typeActualStr为退货不退款时，金额显示0
      dealMessage: false, // 退款信息单独处理
      // job: true,
      show: false, // 控制弹窗
      operation: true, // 控制操作按钮显隐（4/5）隐藏
      modifyApply: true, // 控制撤销申请（3/9）隐藏
      shippingMode: '', // B2C 小电 / B2B2C大电
      returnMessage: 99, // 退款状态（11发起退货退款/13&&16商家拒绝及时处理/12商家同意退货请填写快递信息/(12)-14.2商家同意已填写快递信息/6撤销退款（退款关闭）/5-15商家同意退款/(6)3受理完成/(11)7退款成功/(7)17等待平台处理/8-5平台拒绝/9-99执行job节点/10-14仅退款等待商家处理）
      shop: {
        state: '您已成功发起退款申请，请耐心等待商家处理',
        productFullName: '', // 商品名称
        productDefaultImageUrl: '', // 商品图片
        productAttrPic: require('@/assets/imgs/pc/camera.png'), // 当前商品规格图片（若productAttrName不为空时，显示此图片
        productAttrName: '', // 商品规格名称（不为空时显示商品规格）
        repairSn: '', // 逆向订单号
        storeName: '', // 商铺名称
        refundAmount: '', // 退款金额
        addTime: '', // 申请时间
        reason: '', // 退款原因
        typeActualStr: '', // 退款退货类型
        description: '',
        handleStatus: 11, // 实时状态值(1.客服/团长审核/3.受理完成/5.驳回（小二拒绝）/6.终止/11.待商家审核退货/12.待用户邮寄商品（商家同意退货）/13&&16.商家驳回退货/14.待商家审核退款/15.商家同意退款/16.商家驳回退款/17.待小二审核/7线下已退款/99系统处理中（执行job节点）/14.2商家同意已填写快递信息（realTimeStatus===14且typeActual===3）/99.9留做平台同意时同意修改此状态)
        orderWorkFlowList: [
          {
            name: '',
            val: ''
          },
          {
            name: '',
            val: ''
          }
        ],
        realTimeStatusContent: ''
      },
      endTime: '', // 数据返回时间戳转变显示
      reason: '', // 驳回原因
      orderProductId: null, // 网单ID (测试: 退货单生成: 47779237 用户撤销申请：47779251 商家同意退货:47779253 用户填写快递信息:47779255 商家驳回退货:47779257 .商家同意退款:47779259 商家驳回退款:47779269 用户修改退货申请:47779303 用户发起小二判定: 47779331 小二同意:47779333 用户超期未处理:47779353 小二驳回:47779335 商家延时退款:47779319 退款成功:47779277)
    }
  },
  watch: {
    returnMessage(value) {
      if (value === 12 || value === 99.9 || value === 14.2 || value === 13 || value === 16 || value === 17 || value === 5) {
        this.modifyApply = false
      } else {
        this.modifyApply = true
      }
      if (value === 6 || value === 15 || value === 7 || value === 3) {
        this.operation = false
      }
    },
  },
  methods: {
    /**
   * @description  跳转协商历史
   */
    toHistory() {
      this.$router.push({ path: '/consult', query: { repairSn: JSON.stringify({ repairSn: this.shop.repairSn, orderProductId: this.orderProductId, storeCode: this.shop.storeCode }) } })
    },
    /**
   * @description  撤销弹窗确定
   */
    sureRevoke() {
      const data = {
        repairSn: this.shop.repairSn // 逆向单号
      }
      personalApi.cancelOrderRepair(data).then(res => {
        if (res.data.success) {
          this.$toast.success('撤销成功')
        } else {
          this.$toast.fail(res.data.message)
        }
        // 重新请求退款详情
        // this.details()
      })
    },
    /**
   * @description  撤销弹窗取消
   */
    onCancel() {
      this.show = false
    },
    /**
   * @description  撤销弹窗显隐
   */
    toResove() {
      // this.show = true
      this.$dialog.confirm({
        title: '提示',
        message: '您确定要撤销本次退货/退款申请？',
        className: 'dialog-custom',
      }).then(() => {
        const data = {
          repairSn: this.shop.repairSn // 逆向单号
        }
        personalApi.cancelOrderRepair(data).then(res => {
          if (res.data.success) {
            this.$toast.success('撤销成功')
            this.details()
          } else {
            this.$toast.fail(res.data.message)
          }
          // 重新请求退款详情
          // this.details()
        }).catch(() => {
          this.$toast.fail('撤销失败!');
        })
      }).catch(() => {
        this.$toast.success('撤销取消');
      });
    },
    /**
   * @description  客服介入/平台介入（填写维权）
   */
    toplatform() {
      this.$router.push({ path: '/equity', query: { repairSn: JSON.stringify({ repairSn: this.shop.repairSn, orderProductId: this.orderProductId }) } })
    },
    /**
   * @description  填写快递信息
   */
    toExpress() {
      this.$router.push({ path: '/return', query: { repairSn: JSON.stringify({ repairSn: this.shop.repairSn, orderProductId: this.orderProductId }) } })
    },
    /**
   * @description  修改申请
   */
    toRefund() {
      this.$router.push({ path: '/refund', query: { repairSn: JSON.stringify({ repairSn: this.shop.repairSn, orderProductId: this.orderProductId, cOrderSn: this.shop.cOrderSn }) } })
    },
    /**
   * @description  退款详情接口
   */
    details() {
      const data = {
        orderProductId: this.orderProductId // 网单id
      }
      personalApi.getOrderRepairsInfo(data).then(res => {
        if (res.data.success) {
          this.shop = res.data.data
          this.shippingMode = res.data.data.shippingMode
          // 状态为3且typeActualStr为退货不退款时，金额显示0
          if (res.data.data.typeActualStr === '退货不退款' && res.data.data.realTimeStatus === 3) {
            this.typeActualStr = false
          }
          // 判断仅退款同意待审核，退货退款待商家同意已填写完快递信息待商家审核
          if (res.data.data.realTimeStatus === 14 && (res.data.data.typeActual !== 3 || (res.data.data.typeActual === 3 && res.data.data.shippingMode === 'B2B2C'))) {
            this.returnMessage = 14.2
          } else {
            this.returnMessage = res.data.data.realTimeStatus
            // this.returnMessage = res.data.data.handleStatus
          }
          console.log('returnMessage', this.returnMessage)
          if (res.data.data.realTimeStatusContent.reason) {
            this.reason = res.data.data.realTimeStatusContent.reason
          }
          // 对realTimeStatusContent中的时间戳进行转化
          if (res.data.data.realTimeStatusContent.finishTime) {
            this.endTime = this.timestampToTime(res.data.data.realTimeStatusContent.finishTime)
          } else if (res.data.data.realTimeStatusContent.endTime && res.data.data.systemTime) {
            if (res.data.data.realTimeStatusContent.endTime >= res.data.data.systemTime) {
              this.endTime = this.formatDuring(res.data.data.realTimeStatusContent.endTime - res.data.data.systemTime)
            } else {
              this.endTime = false
            }
          } else if (res.data.data.realTimeStatusContent.expectTime) {
            this.endTime = this.timestampToTime(res.data.data.realTimeStatusContent.expectTime)
          }
        } else {
          // this.job = false
          if (res.data.message === '退款信息不存在') {
            this.dealMessage = true
          } else {
            this.$toast.fail(res.data.message)
          }
        }
      })
    },
    /**
   * @description  时间戳转天时分
   */
    formatDuring(mss) {
      var days = parseInt(mss * 1000 / (1000 * 60 * 60 * 24));
      var hours = parseInt((mss * 1000 % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
      var minutes = parseInt((mss * 1000 % (1000 * 60 * 60)) / (1000 * 60));
      return days + ' 天 ' + hours + ' 时 ' + minutes + ' 分 ';
    },
    /**
   * @description  时间戳转年月日时分
   */
    timestampToTime(timestamp) {
      const date = new Date(timestamp * 1000);// 时间戳为10位需*1000，时间戳为13位的话不需乘1000
      const Y = date.getFullYear() + '年';
      const M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '月';
      const D = (date.getDate() < 10 ? '0' + date.getDate() : date.getDate()) + '日 ';
      const h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
      const m = (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes());
      return Y + M + D + h + m;
    },
    /**
   * @description  联系客服
   */
    contact() {
    // 如果是海尔app隐藏客服头部栏，使用app内头部栏，否则使用客服工具头部栏
      this.$store.dispatch('goPageByType', {
        type: 29,
        params: {
          accountId: this.shop.storeCode || '',
          sceneId: 'cj095'
        }
      })
      // }
    },
    /**
     * @description 验证传入的88码是否开通商家客服
    */
    isCanGetCustomer(storeCode) {
      return new Promise((resolve, reject) => {
        const data = {
          storeCode: storeCode
        };
        personalApi.getCustomer(data).then(res => {
          if (res.data.success) {
            resolve(res.data.result)
          }
        })
      })
    },
    /**
     * 价格保留两位小数
     */
    toFixedTwo(number) {
      if (number !== '') {
        const price = Number(number).toFixed(2);
        return price
      } else {
        return ''
      }
    },
    /**
     * 自定义路由跳转
     */
    // onClickLeft() {
    //   if (this.keepBack) {
    //     this.$router.go(-1)
    //   } else {
    //     this.$router.replace({ path: '/repair' })
    //   }
    // },
  },
}
